/* @tailwind base; */

@tailwind base;
@tailwind components;
@tailwind utilities;


body,html{
  overflow-x: hidden;
  height: 100%;
  font-family: sans-serif !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    
}

a {
  text-decoration: none !important;
}

[data-whatinput='mouse'] :focus,
[data-whatinput='touch'] :focus {
    outline: none;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.css-g5r812 {
  z-index: 0 !important;
}
