}
.section {
    height: 100vh;
  }
  
  .section.about-us {
    position: relative;
    overflow: hidden;
    height: 100vh;
    background-image:url("https://tse1.mm.bing.net/th?id=OIP.cfG6PvOizyYIyB7RGF4OgQHaFj&pid=Api&P=0");
  }

  .about-us-bg {
    position: absolute;
    left: auto;
    top: 0%;
    right: 0%;
    bottom: 0%;
    z-index: 0;
    overflow: visible;
    width: 100%;
    height: 100vh;
    /* opacity: 0.5; */
    -webkit-filter: grayscale(25%);
    filter: grayscale(25%);
  }
  
  p{
      margin-top: 0;
  }
  .about-us-texts-wrapper {
    position: relative;
    z-index: 1;
