  }

  .marquee-two {
    position: relative;
    width: 100vw;
    max-width: 100%;
    height: 200px;
    overflow-x: hidden;
  }
  
  .track-two {
    position: absolute;
    white-space: nowrap;
    will-change: transform;
    -webkit-animation: marquee-two 50s linear infinite;
            animation: marquee-two 50s linear infinite;
  }
  
  @-webkit-keyframes marquee-two {
    from { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
    to { -webkit-transform: translateX(0); transform: translateX(0); }
  }
  
  @keyframes marquee-two {
    from { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
    to { -webkit-transform: translateX(0); transform: translateX(0); }
  }

