#hero-area{background-image:url(./img/hero-area.svg);
    background-position:center center;background-repeat:no-repeat;background-size:cover;min-height:650px;position:relative;overflow:hidden;padding:200px 0 80px}
#hero-area .contents .head-title{color:#222;font-size:42px;font-weight:700;line-height:60px;margin-bottom:10px}
#hero-area .contents .header-button{margin-top:20px;color:#222}
#hero-area .contents .header-button .btn{margin-right:10px}
.sloder-img{background:#34363a}

.btn-common {
    background-color: #f63854;
    position: relative;
    z-index: 1;
    text-transform: uppercase;
}

.btn-border {
    color: #f63854;
    background-color: transparent;
    border: 1px solid #f63854;
    border-radius: 30px;
    text-transform: uppercase;
}
a {
    
    text-decoration: none;
    
}
.btn {
    font-size: 14px;
    padding: 10px 30px;
    text-align: center;
    vertical-align: middle;
    font-weight: 400;
    color: rgb(8, 8, 8);
    border-radius: 30px;
    line-height: 1.5;
    transition: all .3s;
    display: inline-block;
}

.btn-border:hover {
    background-color: #f63854;
}
